import React, { useRef, useEffect } from 'react';
import './components/style/Reset.scss';
import './App.scss';

import PositionContainer from './containers/PositionContainer';
import Home from './components/Home';
import Profile from './components/Profile';
import Portfolio from './components/Portfolio';

export type scrollType = {
  target: Element | null,
  viewHeight: number,
  scrollTop: number,
}
function App() {
  const scrollInfo = useRef<scrollType | null>(null);

  useEffect(() => {
    const target = document.querySelector('.App');
    if(!target) { throw new Error('Unmounted component'); }
    scrollInfo.current = {
      target,
      viewHeight: target.clientHeight,
      scrollTop: 0
    }
  },[]);

  return (
    <div className="App">
      <PositionContainer scrollInfo={scrollInfo} />
      <div className='menu'></div>
      <section className='contents'>
        <Home />
        <Profile />
        <Portfolio />
      </section>
    </div>
  );
}

export default App;
