import React, { useRef } from 'react';
import { findDOMNode } from 'react-dom';
import $ from 'jquery';
import Position from '../components/Position';
import { scrollType } from '../App';

type ScrollInfo = {
    scrollInfo: React.MutableRefObject<scrollType | null>
}
function PositionContainer ({scrollInfo}: ScrollInfo) {
    const onClick = (index: number) => (e:React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        if(!scrollInfo || !scrollInfo.current || !scrollInfo.current.target){ throw new Error('Uncreated ref') };
        const target = scrollInfo.current.target
        const nodes = target.querySelectorAll('.contents section');
        let totHeight = 0;
        for(let i=0; i<index-1; i++) {
            totHeight += nodes[i].clientHeight;
        }
        // DOM 조작 안하고 jquery 안 쓰는 방법 찾기
        $(target).animate({
            scrollTop: totHeight
        }, 800);
        target.querySelector('.active')?.classList.remove('active');
        e.currentTarget.parentElement?.classList.add('active');
    }

    return (
        <Position onClick={onClick} />
    )
}
export default PositionContainer;